<template>
  <div class="list-panel" :class="classes">
    <div class="list-panel-header" v-show="showHeader && (title || $slots.header)">
      <slot name="header">
        <span class="list-panel-header-text">
          {{title}}
        </span>
      </slot>
      <div class="list-panel-header-buttons">
        <slot name="button"></slot>
        <button @click="toggleCollapse" class="list-panel-button-collapse" v-if="showCollapse"></button>
      </div>
    </div>
    <div class="list-panel-body">
      <slot></slot>
    </div>
    <div class="list-panel-footer" v-show="!isCollapsed || !collapseFooter">
      <slot name="footer"></slot>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name: 'ListPanel',
  props: {
    // æ é¢æå­
    title: {
      type: String
    },
    // è®¾ç½®æ¯å¦æ¶èµ·åå®¹
    collapsed: {
      type: Boolean,
      default: false
    },
    // æ¯å¦æ¾ç¤ºæ¶èµ·æé®
    showCollapse: {
      type: Boolean,
      default: true
    },
    // å¨æ¶èµ·æ¶ï¼æ¯å¦åæ¶æ¶èµ· footer é¨å
    collapseFooter: {
      type: Boolean,
      default: false
    },
    // æ¯å¦æ¾ç¤ºå¤´é¨
    showHeader: {
      type: Boolean,
      default: true
    },
    // è®¾ç½®æ¯å¦å¤é
    multi: {
      type: Boolean,
      default: false
    },
    // è®¾ç½®éä¸­é¡¹
    selected: {
      type: [String, Number, Array, Object]
    },
    // èªå®ä¹æ ·å¼
    customClass: {
      type: String
    },
    // æ¯å¦åªè¯»ï¼å¨åªè¯»æ¶ä¸è½éè¿é¼ æ ç¹å»è®¾ç½®é¡¹çéä¸­ç¶æ
    readonly: {
      type: Boolean,
      default: false
    },
    // åå®¹æ¯å¦è¡åæ¾ç¤ºï¼ä¸è¡å¯ä»¥æ¾ç¤ºå¤ä¸ªé¡¹ï¼
    inline: {
      type: Boolean,
      default: false
    },
    // å¤æ­é¡¹æ¯å¦ç¸ç­ï¼å¨å¼ä¸ºå¯¹è±¡æ¶ä½¿ç¨
    equal: {
      type: Function,
      default: (a, b) => {
        return a === b
      }
    }
  },
  provide() {
    return {
      event: this.eventBus,
      equal: this.equal
    }
  },
  data() {
    return {
      // æ¯å¦æå 
      isCollapsed: false,
      // éä¸­çæ°æ®éå
      selectedSet: [],
      eventBus: new Vue()
    }
  },
  mounted() {
    this.isCollapsed = this.collapsed
    this.eventBus.$on('item-click', this.onItemClick)

    if (this.selected) {
      this.setSelectedItem(this.selected)
    }
  },
  watch: {
    selected(val) {
      this.setSelectedItem(val)
    }
  },
  computed: {
    classes() {
      return {
        'list-panel-collapsed': this.isCollapsed,
        'list-panel-readonly': this.readonly,
        'list-panel-inline': this.inline,
        [this.customClass || '']: true
      }
    }
  },
  methods: {
    setSelectedItem(selected) {
      if (Array.isArray(this.selected)) {
        this.selectedSet = this.selected.slice(0)
      } else {
        this.selectedSet = [this.selected]
      }

      // è§¦åé»è®¤çéä¸­
      this.eventBus.$emit('value-change', this.selectedSet)
    },
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed
    },
    onItemClick(value) {
      if (this.readonly) {
        // åªè¯»æ¶ä¸ååºç¹å»äºä»¶
        return
      }
      if (this.multi) {
        // éæ©å¤ä¸ª
        let idx = this.selectedSet.indexOf(value)

        if (idx === -1) {
          // å¦ææ²¡æéä¸­ï¼åæ·»å 
          this.selectedSet.push(value)
        } else {
          // å¦æå·²ç»éä¸­ï¼åç§»é¤
          this.selectedSet.splice(idx, 1)
        }
      } else {
        // éä¸­åä¸ª
        this.selectedSet = [value]
      }
      this.eventBus.$emit('value-change', this.selectedSet)
      let selected = this.selectedSet.slice(0)
      this.$emit('update:selected', selected)
      this.$emit('change', selected)
    }
  },
  beforeDestroy() {
    this.eventBus.$off('itemClick', this.onItemClick)
    this.eventBus = undefined
    this.selectedSet = undefined
  }
}
</script>

<style lang="less">
@border-color: #cccccc;
.list-panel {
  border: 1px solid @border-color;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
}

.list-panel-button-collapse {
  width: 24px;
  height: 24px;
  text-align: center;
  padding: 0;
  border: none;
  background: none;
  outline: none;

  &:hover {
    border: 1px solid #AAAAAA;
    border-radius: 50%;
  }

  &:after {
    content: '>>';
    transform: rotateX(-180deg) rotateZ(90deg);
    display: inline-block;
    transition: transform 618ms;
  }
}

.list-panel-header {
  padding: 10px 15px;
  border-bottom: 1px solid @border-color;
}

.list-panel-footer {
  &:not(:empty) {
    padding: 10px 15px;
    border-top: 1px solid @border-color;
  }
}

.list-panel-header-buttons {
  float: right;
}

.list-panel-body {
  padding: 10px 15px;

  &:after {
    content: ' ';
    display: block;
    clear: both;
  }
}

.list-panel-collapsed {
  .list-panel-button-collapse {
    &:after {
      transform: rotateX(0deg) rotateZ(90deg);
    }
  }

  .list-panel-header {
    border-bottom: none;
  }

  .list-panel-body {
    display: none;
  }
}

.list-panel-item {
  padding: 5px;
  border-bottom: 1px solid #eeeeee;
  border-left: 3px solid transparent;
  margin: 5px 0;
  transition: border-color 618ms;

  &:hover {
    border-bottom-color: #AAAAAA;
  }
}

.list-panel-item-selected {
  border-left: 3px solid #67839c;
}

.list-panel-inline {
  .list-panel-item {
    float: left;
    margin: 5px;
    border: 1px solid #eeeeee;

    &:hover {
      border-color: #AAAAAA;
    }
  }

  .list-panel-item-selected {
    border-color: darkolivegreen;
  }
}

</style>
